<template lang="html">
  <div id="pageBox" v-if="!pageLoading">
    <div id="page">
      <div class="bannerBox">
        <img src="../assets/img/banner.jpeg" alt="">
      </div>
      <div class="detailTitle pd14">
        哈哈哈哈上打瞌睡打了卡就哭了多久阿里斯顿阿克苏河的卡号开始贷记卡是大叔控撒啊可视电话卡刷卡
      </div>
      <div class="titleNote pd14">
        <div class="reAndHeat">
          <span class="re">已更新6期 共6期 &nbsp;|&nbsp; </span>
          <span class="heat">41.4万人气</span>
        </div>
        <div class="tagBox">
          <span class="tagItem">
            <i class="iconfont icon-gou"></i> 永久回听
          </span>
          <span class="tagItem">
            <i class="iconfont icon-gou"></i> 已完结
          </span>
        </div>
        <div class="moneyBox">
          ¥ 39.9
        </div>
      </div>
      <div class="navBoxHidden" v-show="navFixed"></div>
      <div id="navBox" class="navBox pd14" :class="{navFixed:navFixed}" ref="nav">
        <ul class="navList">
          <li class="navItem" :class="{active:nowTab==1}">
            <span @click="tabChange(1)">简介</span>
          </li>
          <li class="navItem" :class="{active:nowTab==2}">
            <span @click="tabChange(2)">目录</span>
          </li>
          <li class="navItem" :class="{active:nowTab==3}">
            <span @click="tabChange(3)">评论</span>
          </li>
        </ul>
      </div>
      <div class="tabPage">
        <ul class="pageList pd14">
          <li class="pageList" v-show="nowTab==1">
            <div class="comTitle">简介</div>
          </li>
          <li class="pageList" v-show="nowTab==2">
            <div class="comTitle">专栏课程</div>
            <Catalog :catalogList="catalogList"></Catalog>
          </li>
          <li class="pageList" v-show="nowTab==3">
            <div class="comTitle">用户评论</div>
            <Comment :commentList="commentList"></Comment>
          </li>
        </ul>
      </div>
      <div class="followOther pd14">
        <div class="followBox">
          <div class="imgBox">
            <img src="" alt="">
          </div>
          <div class="followContent">
            <div class="followTitle overflow1">
              阿克苏河打卡机刷卡的啦睡觉阿娇实打实的卡上的贺卡收到
            </div>
            <div class="followNote overflow1">
              阿上课讲话的卡，阿斯顿哈，阿世界的哈
            </div>
          </div>
          <div class="btnBox" @click="followFun" :class="{noFollow:fStatus==1,follow:fStatus==2}">
            {{fStatus==1?"关注":"已关注"}}
          </div>
        </div>
      </div>
      <div class="maybeLike pd14">
        <div class="comTitle">猜你喜欢</div>
        <CommonList :listData="CommonList"></CommonList>
      </div>
      <div class="footBox">
        <div class="collection" @click="collectFun">
          <div class="iconBox">
            <i class="iconfont" :class="{'icon-shoucang0':!colloct,'icon-shoucang1':colloct}"></i>
          </div>
          <div class="collectText">收藏</div>
        </div>
        <div class="subscribe">
          订阅专栏
        </div>
      </div>
      <div class="action" @click="actionShow=true">
        <i class="iconfont icon-gengduo importantColor"></i>
      </div>
      <div class="actionPage" v-show="actionShow"  @click.self="actionShow=false"></div>
      <div class="actionBtnBox" :class="{show:actionShow,hidden:!actionShow}">
        <div class="btnItem">
          <div class="iconBox">
            <i class="iconfont icon-kefu importantColor"></i>
          </div>
          <span class="name">联系客服</span>
        </div>
        <div class="btnItem">
          <div class="iconBox" @click="goHome">
            <i class="iconfont icon-shouye importantColor"></i>
          </div>
          <span class="name">回到首页</span>
        </div>
      </div>
    </div>
    <div id="pageloading" v-if="pageLoading">

    </div>
  </div>
</template>

<script>
import Catalog from "@/components/catalog";
import CommonList from "@/components/commonList";
import Comment from "@/components/comment";

export default {
  components:{
    Catalog,
    CommonList,
    Comment
  },
  data(){
    return {
      catalogList:[{},{},{},{}],
      commentList:[{isZan:0},{isZan:0},{isZan:0},{isZan:0}],
      CommonList:[
        {imgUrl:"",title:"akhsdkasahsdhkasd",note:"最近更新9期",pNum:"1.9万人气",money:"¥999",id:1},
        {imgUrl:"",title:"kashddakshdjkasnkdnaks",note:"最近更新9期",pNum:"1.9万人气",money:"¥999",id:2},
        {imgUrl:"",title:"kashddakshdjkasnkdnaks",note:"最近更新9期",pNum:"1.9万人气",money:"¥999",id:3},
        {imgUrl:"",title:"asdaksdjasd",note:"最近更新9期",pNum:"1.9万人气",money:"¥999",id:4},
        {imgUrl:"",title:"asjdkasdkjasdasdad",note:"最近更新9期",pNum:"1.9万人气",money:"¥999",id:5},
        {imgUrl:"",title:"kasjdljalsdjlkjslkdjajl",note:"最近更新9期",pNum:"1.9万人气",money:"¥999",id:6},
      ],
      nowTab:1,
      fStatus:2,
      pageLoading:true,
      navFixed:false,
      actionShow:false,
      colloct:false
    }
  },
  methods:{
    // 回到首页
    goHome(){
      this.$router.push({
        path:"/"
      })
    },
    tabChange(tabNum){
      this.nowTab = tabNum;
    },
    // 点击关注
    followFun(){
      if(this.fStatus == 1){
        this.fStatus = 2;
      }else{
        this.fStatus = 1;
      }
    },
    // 收藏
    collectFun(){
      this.colloct = !this.colloct;
    },
    getDataFun(){
      var time = new Date().getTime();
      var url = "https://c.y.qq.com/musichall/fcgi-bin/fcg_yqqhomepagerecommend.fcg?g_tk=5381&uin=0&format=jsonp&jsonpCallback=jsonp1&inCharset=utf-8&outCharset=utf-8&notice=0&platform=h5&needNewCode=1&_="+time;
      this.pageLoading = true;
      this.$jsonP(url,"jsonp1",(res)=>{
        this.pageLoading = false;
        // this.allData = res.data;
        this.$nextTick(()=>{
          this.windwoScroll();
        })
      })
    },
    windwoScroll(){
      let navOffsetTop = this.$refs.nav.offsetTop;
      window.onscroll = () => {
        let winScrollY = window.scrollY;
        if(navOffsetTop <= winScrollY){
          this.navFixed = true;
        }else{
          this.navFixed = false;
        }
      }
    },
    init(){
      this.getDataFun();

    }
  },
  created(){

  },
  mounted(){
    this.init();
  },
  watch:{
    '$route'(to, from) {
        if(to.name === '详情'){
          this.init();
        }
    }
  }
}
</script>

<style lang="less" scoped>
  #pageBox{
    #page{
      padding-bottom:0.56rem;
      .comTitle{
        font-size:0.18rem;
        font-weight: bold;
        margin:20px 0;
      }
      .bannerBox{
        height:1.85rem;
        img{
          width:100%;
        }
      }
      .detailTitle{
        margin-top:20px;
        width:100%;
        height:0.48rem;
        line-height: 0.24rem;
        font-size:0.18rem;
        font-weight: bold;
        color:#000;
        display: -webkit-box;
        overflow: hidden;
        text-overflow: ellipsis;
        -webkit-box-orient: vertical;
        -webkit-line-clamp: 2;
      }
      .titleNote{
        margin-top:10px;
        position:relative;
        padding-bottom:20px;
        border-bottom:1px solid #dedede;
        .reAndHeat{
          font-size:0.13rem;
          .re{
            color:#999;
          }
          .heat{
            color:red;
          }
        }
        .tagBox{
          margin-top:4px;
          .tagItem{
            color:#999;
            font-size:0.13rem;
            margin-right:10px;
            i{
              color:#30C957;
            }
          }
        }
        .moneyBox{
          position:absolute;
          bottom:20px;
          right:0.24rem;
          line-height: 0.2rem;
          font-size:0.2rem;
          font-weight: bold;
          color:red;
        }
      }
      .navBoxHidden{
        height:60px;
      }
      .navBox{
        width:100%;
        border-bottom:1px solid #dedede;
        &.navFixed{
          position:fixed;
          top:0;
          left:0;
          background:#f9f9f9;
          z-index: 100;
        }
        .navList{
          width:100%;
          display: flex;
          .navItem{
            flex:1;
            text-align: center;
            span{
              line-height: 60px;
              font-size:18px;
              font-weight: bold;
            }
            &.active{
              span{
                position:relative;
                display: inline-block;
                height:100%;
                color:#31c27c;
                &:after{
                  position:absolute;
                  left:0;
                  bottom:-1px;
                  content:"";
                  width:100%;
                  height:2px;
                  background:#31c27c;
                }
              }
            }
          }
        }
      }
      .followOther{
        width:100%;
        margin:20px 0;
        .followBox{
          width:100%;
          height:90px;
          padding:15px;
          border-radius: 8px;
          padding-right:70px;
          box-shadow: 0 0 14px 4px #ededed;
          position:relative;
          display: flex;
          .imgBox{
            width:60px;
            height:60px;
            background:#999;
            border-radius: 50%;
          }
          .followContent{
            flex:1;
            padding:0 10px;
            overflow:hidden;
            padding-top:2px;
            .followTitle{
              width:100%;
              font-size:20px;
              font-weight: bold;
            }
            .followNote{
              font-size:14px;
              color:#999;
            }
          }
          .btnBox{
            position:absolute;
            width:60px;
            height:24px;
            right:14px;
            top:50%;
            margin-top:-12px;
            font-size:12px;
            line-height: 24px;
            text-align: center;
            border-radius: 12px;
            border:1px solid #31c27c;
            &.noFollow{
              color:#31c27c;
            }
            &.follow{
              color:#fff;
              background:#c4c4c4;
              border-color:#c4c4c4;
            }
          }
        }
      }
      .footBox{
        position:fixed;
        left:0;
        bottom:0;
        width:100%;
        height:0.56rem;
        display:flex;
        background:#fff;
        .collection{
          width:0.56rem;
          text-align: center;
          color:#666;
          padding-top:0.03rem;
          i{
            font-size:0.2rem;
            color:#999;
            &.icon-shoucang1{
              color:red;
            }
          }
          .iconfont{
            line-height: 0.3rem;
          }
          .collectText{
            font-size:0.12rem;
          }
        }
        .subscribe{
          flex:1;
          background:red;
          color:#fff;
          font-size:0.16rem;
          text-align: center;
          line-height: 0.56rem;
        }
      }
      .action{
        position:fixed;
        width:0.5rem;
        height:0.5rem;
        background:#fff;
        border-radius: 50%;
        box-shadow: 0 0 10px 1px #dedede;
        right:0.2rem;
        bottom:1rem;
        text-align: center;
        line-height: 0.5rem;
        z-index: 98;
        i{
          font-size:0.24rem;
        }
      }
      .actionPage{
        position: fixed;
        top:0;
        left:0;
        right:0;
        bottom:0;
        background:rgba(0,0,0,0.4);
        z-index: 99;
      }
      .actionBtnBox{
        position: fixed;
        left:0;
        bottom:0;
        width:100%;
        height:1.2rem;
        background:#fff;
        border-radius:10px 10px 0 0;
        display: flex;
        padding-top:0.2rem;
        transition: all ease-out 200ms;
        z-index: 100;
        &.show{
          transform: translateY(0);
        }
        &.hidden{
          transform: translateY(100%);
        }
        .btnItem{
          flex:1;
          text-align: center;
          .iconBox{
            width:0.6rem;
            height:0.6rem;
            border-radius: 50%;
            box-shadow: 0 0 10px 1px #dedede;
            margin:0 auto;
            line-height: 0.6rem;
            i{
              font-size:0.3rem;
            }
          }
          .name{
            display: inline-block;
            color:#999;
            font-size:0.12rem;
            margin-top:0.08rem;
          }
        }
      }
    }
  }
</style>



