<template lang="html">
  <div id="pageBox">
    <ul class="mentList">
      <li class="mentItem" v-for="(v,i) in commentList" :key="i">
        <div class="imgBox">
          <div class="headImg">
            <img src="" alt="">
          </div>
        </div>
        <div class="mentContent">
          <div class="nameAndTime">
            <span class="name">伊凡</span>
            <span class="time">2019-04-11 15:12</span>
          </div>
          <div class="mentText overflow2">
            老师说的对，表达力就是一个移动硬盘，期待老师接下来的课
          </div>
          <div class="reply">
            <div class="replyName">
              静雅-静静
            </div>
            <div class="replyText overflow2">
              加油哦，克服紧张，相信你可以找到合适的工作的(*^_^*)
            </div>
          </div>
        </div>
        <div class="like" @click="clickZan(v)">
          <i class="iconfont icon-zan icon" :class="{zan:v.isZan}"></i>
          <span class="likeNum">1</span>
        </div>
      </li>
    </ul>
    <div class="loadingMore">
      加载更多
    </div>
  </div>
</template>

<script>
export default {
  props:["commentList"],
  data(){
    return {

    }
  },
  methods:{
    clickZan(obj){
      obj.isZan = obj.isZan == 1 ? 0 : 1;
    }
  }
}
</script>

<style lang="less" scoped>
  #pageBox{
    .mentList{
      .mentItem{
        display: flex;
        position:relative;
        margin-bottom:30px;
        &:last-child{
          margin-bottom:20px;
        }
        .imgBox{
          width:0.5rem;
          .headImg{
            width:0.5rem;
            height:0.5rem;
            background:#999;
            border-radius: 50%;
            img{
              width:100%;
            }
          }
        }
        .mentContent{
          flex:1;
          padding-left:14px;
          overflow:hidden;
          .nameAndTime{
            color:#999;
            font-size:14px;
            .name{
              margin-right:10px;
            }
          }
          .mentText{
            color:#666;
            margin-top:4px;
          }
          .reply{
            background:#ededed;
            margin-top:4px;
            padding:10px;
            border-radius: 5px;
            font-size:14px;
            .replyName{
              color:#5050F3;
              margin-bottom:4px;
            }
            .replyText{
              color:#666;
            }
          }
        }
        .like{
          position:absolute;
          top:0;
          right:0;
          line-height: 20px;
          font-size:0;
          color:#999;
          .icon{
            font-size:18px;
            &.zan{
              color:#FF4444;
            }
          }
          .likeNum{
            font-size:16px;
            line-height: 16px;
          }
        }
      }
    }
  }
</style>



