<template lang="html">
  <div id="pageBox">
    <ul class="list">
      <li class="item" v-for="(v,i) in catalogList" :key="i">
        <div class="imgBox">
          <img src="" alt="">
        </div>
        <div class="content">
          <div class="title overflow2">
            阿克苏河打卡
          </div>
          <div class="tagAndstatus">
            <span class="tagItem voice">音频</span>
            <span class="tagItem try">试听</span>
            <span class="status">未学习</span>
          </div>
        </div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props:["catalogList"],
  data(){
    return {

    }
  },
  methods:{

  }
}
</script>

<style lang="less" scoped>
  #pageBox{
    .list{
      width:100%;
      .item{
        width:100%;
        padding-bottom:20px;
        margin-bottom:20px;
        border-bottom:1px solid #dedede;
        display: flex;
        &:last-child{
          border:none;
        }
        .imgBox{
          width:1.6rem;
          max-width:160px;
          height:0.9rem;
          background:#999;
        }
        .content{
          flex:1;
          padding-top:4px;
          padding-left:10px;
          overflow:hidden;
          .title{
            font-size:0.16rem;
            font-weight: bold;
          }
          .tagAndstatus{
            margin-top:16px;
            width:100%;
            overflow:hidden;
            .tagItem{
              float:left;
              margin-right:4px;
              font-size:0.12rem;
              line-height: 0.12rem;
              padding:2px 6px;
              border-radius: 2px;
              border:1px solid #dedede;
              &.voice{
                color:#4444FF;
                border-color:#4444FF;
              }
              &.try{
                color:#E4C64E;
                border-color:#E4C64E;
              }
            }
            .status{
              float:right;
              margin-right:4px;
              color:#999;
              line-height: 0.2rem;
              font-size:0.12rem;
            }
          }
        }
      }
    }
  }
</style>



